import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import {
    Accordion,
    AccordionItem,
    AccordionItemTitle,
    AccordionItemBody,
} from 'react-accessible-accordion';

// Demo styles, see 'Styles' section below for some notes on use.
import 'react-accessible-accordion/dist/fancy-example.css';
import MetaTags from 'react-meta-tags';


class Preguntas extends React.Component{
  componentDidMount() {
    window.scrollTo(0, 0)
  }
  render(){
    return(
     <div className="cotenido_general">
       <MetaTags>
         <title>Preguntas frecuentes sobre Super| Super Envigado</title>
         <meta property="og:title" content="Preguntas frecuentes sobre Super" />
       </MetaTags>
        <Container>
          <Row>
            <Col xs="12" className='tittle'><h2 className='tittleh'>Preguntas frecuentes</h2></Col>
            <Col xs="12" className='tittle'><div className='sublineP' div></div></Col>
            <Col md="8">

           <Accordion className="marginRow marginRow_bottom">
          <AccordionItem>
              <AccordionItemTitle>
                  <h5 className="bold">Super es una beca?</h5>
              </AccordionItemTitle>
              <AccordionItemBody>
            <Col md='12' className='marginTB'>
              <p className='justify'>
              No, Super es un estmulo a la educacin superior, que consiste en un apoyo
              econmico que se les entrega a los beneficiarios, a travs del cual se les cubre un
              porcentaje o una parte de la matricula semestral.
              </p>
            </Col>
              </AccordionItemBody>
          </AccordionItem>

           <AccordionItem>
              <AccordionItemTitle>
                  <h5 className="bold">Qu es un crdito condonable?</h5>
              </AccordionItemTitle>
              <AccordionItemBody>
                 <Col md="12"  className='marginTB'>
              <p className='justify'>
              Un crdito condonable es como te financiaremos un porcentaje de tu matricula,
                siempre y cuando culmines la carrera que escojas, obtengas el ttulo profesional y
                cumplas con los requisitos de condonacin, no tendrs que pagarle al Municipio los
                desembolsos hechos.
              </p>
            </Col>
              </AccordionItemBody>
          </AccordionItem>

           <AccordionItem>
              <AccordionItemTitle>
                  <h5 className="bold">Qu hacer sino apareces como preseleccionado?</h5>
              </AccordionItemTitle>
              <AccordionItemBody>
                   <Col md='12' className='marginTB'>
              <p className='justify'>
              No te desanimes, sino resultas preseleccionado puedes buscar otras opciones de
              financiacin, o puedes esperar a una prxima convocatoria de Super para volver a
              intentarlo, siempre y cuando no hayas iniciado ningn semestre acadmico.
              </p>
            </Col>
              </AccordionItemBody>
          </AccordionItem>

           <AccordionItem>
              <AccordionItemTitle>
                  <h5 className="bold">Los beneficiarios pueden cambiar de programa o de institucin de
            educacin superior?</h5>
              </AccordionItemTitle>
              <AccordionItemBody>
                  <Col md='12' className='marginTB'>
              <p className='justify'>
              No, el programa Super adjudica sus crditos condonables para una carrera y universidad especfica, por lo que no es posible realizar ningn tipo de cambio.
              </p>
            </Col>
              </AccordionItemBody>
          </AccordionItem>

           <AccordionItem>
              <AccordionItemTitle>
                  <h5 className="bold">Qu promedio exige el programa para los beneficiarios?</h5>
              </AccordionItemTitle>
              <AccordionItemBody>
                 <Col md='12' className='marginTB'>
              <p className='justify'>
              El promedio exigido por el programa para la permanencia es mnimo de 3,5 para el promedio semestral.
              </p>
            </Col>
              </AccordionItemBody>
          </AccordionItem>

           <AccordionItem>
              <AccordionItemTitle>
                  <h5 className="bold">Qu pasa si el beneficiario no cumple con los requisitos para condonar el crdito?</h5>
              </AccordionItemTitle>
              <AccordionItemBody>
                  <Col md='12' className='marginTB'>
              <p className='justify'>En caso de que un beneficiario del programa no cumpla con el requisito de condonacin o pierda su condicin de beneficiario, deber pagar al municipio el total del valor por concepto de matrculas desembolsadas hasta la fecha.
              </p>
            </Col>
              </AccordionItemBody>
          </AccordionItem>

      </Accordion>
            </Col>
            <Col md="4" className="marginRow marginRow_bottom">
            <img src="https://cidpullzonestorage.b-cdn.net/supere/IMG-Preguntasfrecuentes.png" alt=""/>
            </Col>
          </Row>

        </Container>
      </div>
  );
  }
}

export default Preguntas;



